<template>
  <layout>
    <div class="card card-primary">
      <div class="card-header">
        <h3 class="card-title">Add Your Data Please</h3>
      </div>
      <form @submit.prevent="contacts.submitForm">
        <div class="card-body">
          <div class="form-group">
            <label>Name</label>
            <input
              required
              v-model="contacts.contactInfo.name"
              type="text"
              class="form-control"
              placeholder="Enter Your Name"
            />
            <p
              class="text-red"
              v-for="error in v$.name.$errors"
              :key="error.$uid"
            >
              {{ error.$message }}
            </p>
          </div>
          <div class="form-group">
            <label>Jop Title</label>
            <input
              required
              v-model="contacts.contactInfo.job"
              type="text"
              class="form-control"
              placeholder="Enter Your Jop Title"
            />
            <p
              class="text-red"
              v-for="error in v$.job.$errors"
              :key="error.$uid"
            >
              {{ error.$message }}
            </p>
          </div>
          <div class="form-group">
            <label for="exampleInputEmail1">Email</label>
            <input
              required
              v-model="contacts.contactInfo.email"
              type="email"
              class="form-control"
              placeholder="Enter Your Email"
            />
            <p
              class="text-red"
              v-for="error in v$.email.$errors"
              :key="error.$uid"
            >
              {{ error.$message }}
            </p>
          </div>
          <div class="form-group">
            <label>Address</label>
            <input
              required
              v-model="contacts.contactInfo.address"
              type="address"
              class="form-control"
              placeholder="Enter Your Address"
            />
            <p
              class="text-red"
              v-for="error in v$.address.$errors"
              :key="error.$uid"
            >
              {{ error.$message }}
            </p>
          </div>
          <div class="form-group">
            <label>About</label>
            <textarea
              required
              v-model="contacts.contactInfo.about"
              type="phone"
              class="form-control"
              placeholder="Write Something About You"
            />
            <p
              class="text-red"
              v-for="error in v$.about.$errors"
              :key="error.$uid"
            >
              {{ error.$message }}
            </p>
          </div>
          <div class="form-group">
            <label>Agreed hourly rate</label>
            <input
              required
              v-model="contacts.contactInfo.rate"
              type="text"
              class="form-control"
              placeholder="Enter Your Hourly Rate"
            />
            <p
              class="text-red"
              v-for="error in v$.rate.$errors"
              :key="error.$uid"
            >
              {{ error.$message }}
            </p>
          </div>
          <div class="form-group">
            <label>Phone</label>
            <input
              required
              v-model="contacts.contactInfo.phone"
              type="phone"
              class="form-control"
              placeholder="Enter Your Phone"
            />
            <p
              class="text-red"
              v-for="error in v$.phone.$errors"
              :key="error.$uid"
            >
              {{ error.$message }}
            </p>
          </div>
          <div class="form-group">
            <label>Image</label>

            <div class="custom-file">
              <input type="file" class="custom-file-input" id="customFile" />
              <label class="custom-file-label" for="customFile"></label>
            </div>
          </div>
        </div>
        <!-- <div class="mx-2">
          <p>Errors:</p>
          <p v-for="error in v$.$errors" :key="error.$uid">
            {{ error.$property }} - {{ error.$message }}
          </p>
        </div> -->
        <div class="card-footer text-center">
          <button
            @click.prevent="submitForm"
            type="submit"
            class="btn btn-primary"
          >
            Add Contact
          </button>
        </div>
      </form>
    </div>
  </layout>
</template>

<script setup>
import useVuelidate from "@vuelidate/core";
// eslint-disable-next-line no-unused-vars
import { required } from "@vuelidate/validators";
import { contacts } from "../../../stores/contactsStore";
import layout from "../../Layout.vue";

const v$ = useVuelidate(contacts.rules, contacts.contactInfo);
const submitForm = async () => {
  const result = await v$.value.$validate();
  if (result) {
    alert("success, form submitted!");
    contacts.addContact();
  } else {
    alert("error, form not submitted");
  }
};

// eslint-disable-next-line no-unused-vars
// const addContact = () => {
//   contacts.addContact();
// };
</script>