import { reactive } from "vue";
import router from "../router/index";
// import useVuelidate from "@vuelidate/core"
import { required } from "@vuelidate/validators"

export const contacts = reactive({
    editedContact: null,

    contacts: [],
    clonedContacts:[],  
    

    contactInfo: {
        name: "",
        job: "",
        email: "",
        address: "",
        phone: "",
        about: "",
        rate: "",
        img: "",
    },

    rules: {
        name: { required },
        job: { required },
        email: { required },
        address: { required },
        phone: { required },
        about: { required },
        rate: { required },
        img: "",
    },

    // v$: () => {useVuelidate(this.rules, this.contactInfo)},

    // submitForm: () => {
    //     async () => {
    //         const result = await this.v$.$validation();
    //         console.log(result);
    //         if(result) {
    //             alert("success, form submitted!");
    //         } else {
    //             alert("error, form not submitted")
    //         }
    //     }
    // },

    addContact(){
        this.contacts.push(this.contactInfo);
        this.clonedContacts=[...this.contacts];
        this.resetContactInfo();
        router.push({ path: "/contacts" }); 
        
    },
    deleteContact(index){
        this.contacts= this.contacts.filter((item, i)=>(index !== i));
        this.clonedContacts=[...this.contacts]
        // this.contacts.splice(index, 1);
    },

    resetContactInfo(){
        this.contactInfo = {
            name: "",
            job: "",
            email: "",
            address: "",
            phone: "",
            about: "",
            rate: "",
            img: "",
        }
    },

    editContact(index){
        this.contactInfo = this.contacts[index];
        this.clonedContacts=[...this.contacts]
        this.editedContact = index;
    },

    updateContact(){
        this.contacts[this.ediedContact] = this.contactInfo;
        this.clonedContacts=[...this.contacts]
        this.resetContactInfo();
        router.push({ path: "/contacts" });
    },

    search(searchText){
        this.contacts = this.clonedContacts.filter((contact) =>
            contact.name.toLowerCase().includes(searchText) ||
            contact.about.toLowerCase().includes(searchText)
        );
    }
})